export enum EGender {
	male = 1,
	female = 2,
	allofThem = 3,
}
export namespace EGender {
	export function getValue(gender: EGender | number): string {
		switch (gender) {
			case EGender.male:
				return "male";
			case EGender.female:
				return "female";
			case EGender.allofThem:
				return "allofThem";
			default:
				return "";
		}
	}
}
