import { Component, OnInit } from "@angular/core";
import { MatTableDataSource } from "@angular/material/table";
import { IGroupModel } from "app/general-setup/models/groups";
import { GroupsService } from "app/general-setup/services/groups.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
	selector: "app-groups-index",
	templateUrl: "./groups-index.component.html",
	styleUrls: ["./groups-index.component.scss"],
})
export class GroupsIndexComponent extends AppHelpers implements OnInit {
	dataSource: MatTableDataSource<IGroupModel>;
	mandatoryColomns: string[] = ["Ser", "id", "arbName", "engName", "actions"];
	isLoaded: boolean = false;
	optionalColomns: string[] = [];
	constructor(private _groupsService: GroupsService) {
		super();
	}

	ngOnInit(): void {}

	override onChangeBusinessAndCompanyId(): void {
		if (this.checkNoBusinessOrCompany())
			this.getList(this.pageFromUrl > 0 ? this.pageFromUrl : this.firstPage);
	}

	getList(page: number, pageSize?: number) {
		this.page.pageIndex = page;
		if (pageSize) this.page.pageSize = pageSize;
		let params: IPageParams = {
			PageNumber: page,
			pageSize: this.page.pageSize,
		};
		this._groupsService.getList(params).subscribe((res: IResponseList<IGroupModel[]>) => {
			if (res.message.messageType == MessageTypes.Success) {
				if (res.data.length == 0 && page > 1) {
					this.getList(this.prevPage);
					return;
				}
				this.dataSource = new MatTableDataSource(res.data);
				this.isLoaded = true;
				this.page.length = res.meta.totalItemCount;
			} else this.errorList(res.message.messages);
		});
	}
	onDelete(group: IGroupModel): void {
		let msg = this.msgTranslate(group.engName, group.arbName);
		this.deleateDialog(msg).subscribe((confirm) => {
			if (confirm) this.delete(group);
		});
	}

	delete(group: IGroupModel) {
		this.showLoading();
		this._groupsService.Delete(group.id).subscribe((res: IResponse) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
				if (this.dataSource.data.length == 1 && this.currentPage > 1)
					this.getList(this.prevPage, this.page.pageSize);
				else this.getList(this.currentPage, this.page.pageSize);
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}
}
