import { ChangeDetectionStrategy, Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import {
  ICompanyCodeModel,
  ICompanyCodeRequest,
} from "app/general-setup/models/company-code";
import { CompanyCodeService } from "app/general-setup/services/company-code.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "app-company-code",
  templateUrl: "./company-code.component.html",
  styleUrls: ["./company-code.component.scss"],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class CompanyCodeComponent extends AppHelpers implements OnInit {
  form: FormGroup;
  submitted: boolean = false;
  constructor(private _companyCodeService: CompanyCodeService) {
    super();
  }

  ngOnInit(): void {
    this.form = this.fb.group({
      id: [0, { nonNallable: true }],
      isHasCode: [false, Validators.required],
      company_ID: [, Validators.required],
    });

    this.formCtrls.company_ID.valueChanges.subscribe((val) => {
      if (val > 0) {
        this.getCodeStatus();
      }
    });
  }
  get formCtrls() {
    return this.form?.controls;
  }

  getCodeStatus() {
    this.showLoading();
    this._companyCodeService
      .getCodeStatus(this.formCtrls.company_ID.value)
      .subscribe((res: IResponse<ICompanyCodeModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          if (res.data == null) {
            this.formCtrls.id.setValue(0);
            this.formCtrls.isHasCode.setValue(false);
          } else {
            this.formCtrls.id.setValue(res.data.id);
            this.formCtrls.isHasCode.setValue(res.data.isHasCode);
          }
        } else {
        }
      });
  }

  submit() {
    this.submitted = true;
    if (this.form.valid) {
      const model: ICompanyCodeRequest = {
        id: this.formCtrls.id.value,
        isHasCode: this.formCtrls.isHasCode.value,
        company_ID: this.formCtrls.company_ID.value,
      };
      model.id == 0 ? this.create(model) : this.update(model);
    }
  }

  create(model: ICompanyCodeRequest) {
    this.showLoading();
    this._companyCodeService
      .create(model)
      .subscribe((res: IResponse<ICompanyCodeModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.addSuccessfullyMsg(res.message.messageTypeName);
        } else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  update(model: ICompanyCodeRequest) {
    this.showLoading();
    this._companyCodeService
      .update(model)
      .subscribe((res: IResponse<ICompanyCodeModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.updateSuccessfullyMsg(res.message.messageTypeName);
        } else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }
}
