import { Component, Input, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import {
  INotificationDetailsModel,
  INotificationDetailsRequest,
} from "app/general-setup/models/person/personal-notification";
import { PersonalNotificationService } from "app/general-setup/services/personal-notification.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
	selector: "app-notification-details",
	templateUrl: "./notification-details.component.html",
	styleUrls: ["./notification-details.component.scss"],
})
export class NotificationDetailsComponent extends AppHelpers implements OnInit {
	@Input() PersonalId: number = 0;
	form: FormGroup;
	submitted: boolean = false;
	mandatoryColomns: string[] = ["Ser", "NotificationType", "actions"];
	isLoaded: boolean = false;
	optionalColomns: string[] = [];
	dataSource: MatTableDataSource<INotificationDetailsModel>;

	constructor(
 		private personalNotificationService: PersonalNotificationService
	) {
		super();
	}

	ngOnInit(): void {
		if (this.checkNoBusinessOrCompany() && this.PersonalId > 0) {
			this.getList(1);
		}

		this.form = this.fb.group({
			id: [0, { nonNallable: true }],
			notificationType: [null, [Validators.required]],
		});
	}

	get formCtrls() {
		return this.form.controls;
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.checkNoBusinessOrCompany() && this.PersonalId > 0) this.getList(1);
	}

	getList(page: number, pageSize?: number) {
		this.page.pageIndex = page;
		if (pageSize) this.page.pageSize = pageSize;
		this.removePageParams();
		let params = {
			PageNumber: page,
			pageSize: this.page.pageSize,
		};
		this.personalNotificationService
			.getNotificationDetails(params)
			.subscribe((res: IResponseList<INotificationDetailsModel[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
					if (res.data.length == 0 && page > 1) {
						this.getList(this.prevPage);
						return;
					}
					this.dataSource = new MatTableDataSource(res.data);
					this.isLoaded = true;
					this.page.length = res.meta.totalItemCount;
				} else this.errorList(res.message.messages);
			});
	}

	submit() {
		this.submitted = true;
		if (this.form.valid) {
			const model: INotificationDetailsRequest = {};
			// model.User.Id == 0 ? this.create(model) : this.update(model);
		}
	}

	create(model: INotificationDetailsRequest) {
		this.showLoading();
		this.personalNotificationService
			.createPersonalNotification(model, this.businessCompanyParams)
			.subscribe((res: IResponse<INotificationDetailsModel>) => {
				if (res?.message?.messageType == MessageTypes.Success) {
					this.addSuccessfullyMsg(res.message.messageTypeName);
				} else this.errorList(res.message.messages);
			});
	}
}
