import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { IReportsModel, IsoCodeRequest } from "app/general-setup/models/iso-code";
import { IsoCodeService } from "app/general-setup/services/iso-code.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
	selector: "app-iso-code",
	templateUrl: "./iso-code.component.html",
	styleUrls: ["./iso-code.component.scss"],
})
export class IsoCodeComponent extends AppHelpers implements OnInit {
	form: FormGroup;
	submitted: boolean = false;
	isoChanged: boolean;
	constructor(private isoCodeService: IsoCodeService) {
		super();
	}

	ngOnInit(): void {
		this.form = this.fb.group({
			reportId: [null, [Validators.required]],
			iso: [null, [Validators.required]],
		});
	}

	get formCtrls() {
		return this.form.controls;
	}

	submit() {
		this.submitted = true;
		if (this.form.valid) {
			const model: IsoCodeRequest = {
				id: this.formCtrls.reportId.value,
				iso: this.formCtrls.iso.value,
			};
			this.update(model);
		}
	}

	onSelectReport(report: IReportsModel) {
		if (report) this.formCtrls.iso.setValue(report.iso);
		else this.formCtrls.iso.setValue(null);
	}

	update(model: IsoCodeRequest) {
		this.showLoading();
		this.isoCodeService.Edit(model).subscribe((res) => {
			if (res?.message?.messageType == MessageTypes.Success) {
				this.isoChanged = true;
				this.clearForm();
				this.addSuccessfullyMsg(res.message.messageTypeName);
			} else this.errorList(res.message.messages);
		});
	}

	clearForm() {
		this.form.reset();
		this.submitted = false;
	}
}
