import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import {
	IPersonalNotificationModel,
	IPersonalNotificationRequest,
} from "app/general-setup/models/person/personal-notification";
import { PersonalNotificationService } from "app/general-setup/services/personal-notification.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
	selector: "app-add-personal-notification",
	templateUrl: "./add-personal-notification.component.html",
	styleUrls: ["./add-personal-notification.component.scss"],
})
export class AddPersonalNotificationComponent extends AppHelpers implements OnInit {
	PersonalId: number = 0;
	form: FormGroup;
	submitted: boolean = false;

	constructor(private personalNotificationService: PersonalNotificationService) {
		super();
	}

	ngOnInit(): void {
		this.activatedRoute.paramMap.subscribe((params) => {
			this.PersonalId = Number(params.get("PersonalId")) ?? 0;
			if (this.checkNoBusinessOrCompany() && this.PersonalId > 0) {
				this.getPersonById();
			}
		});

		this.form = this.fb.group({
			Personid: [null],
			id: [0, { nonNallable: true }],
			Name: [null, [Validators.required]],
			PassWord: [null, [Validators.required]],
			Is_Active: [false],
		});
	}

	get formCtrls() {
		return this.form.controls;
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.checkNoBusinessOrCompany() && this.PersonalId > 0) this.getPersonById();
	}

	submit() {
		this.submitted = true;
		if (this.form.valid) {
			const model: IPersonalNotificationRequest = {};
			// model.User.Id == 0 ? this.create(model) : this.update(model);
		}
	}

	create(model: IPersonalNotificationRequest) {
		this.showLoading();
		this.personalNotificationService
			.createPersonalNotification(model, this.businessCompanyParams)
			.subscribe((res: IResponse<IPersonalNotificationModel>) => {
				if (res?.message?.messageType == MessageTypes.Success) {
					this.addSuccessfullyMsg(res.message.messageTypeName);
				} else this.errorList(res.message.messages);
			});
	}

	update(model: IPersonalNotificationRequest) {
		this.showLoading();
		this.personalNotificationService
			.updatePersonalNotificationy(model, this.businessCompanyParams)
			.subscribe((res: IResponse<IPersonalNotificationModel>) => {
				if (res.message.messageType == MessageTypes.Success)
					this.updateSuccessfullyMsg(res.message.messageTypeName);
				else this.errorList(res.message.messages);
			});
	}

	getPersonById() {}
}
