import { Component, Injector, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { IActivationSetupModel } from "app/general-setup/models/activation-setup";
import {
	IAttachmentTypesModel,
	IAttachmentTypesRequest,
} from "app/general-setup/models/attachment-types";
import { AttachmentTypesService } from "app/general-setup/services/attachment-types.service";
import { RadioButtonModel } from "app/shared/components/dynamic-form-field/radio-button/radio-button.component";
import { EGender } from "app/shared/enums/gender.enum";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";
import { CustomValidator } from "app/shared/validators/custom.validator";

@Component({
	selector: "app-attachment-types",
	templateUrl: "./attachment-types.component.html",
	styleUrls: ["./attachment-types.component.scss"],
})
export class AttachmentTypesComponent extends AppHelpers implements OnInit {
	form: FormGroup;
	submitted: boolean = false;
	dataSource: MatTableDataSource<IAttachmentTypesModel>;
	mandatoryColomns: string[] = ["Ser", "id", "ar_Name", "en_Name", "genderName", "actions"];
	isLoaded: boolean = false;
	optionalColomns: string[] = [];
	constructor(private attachmentTypsService: AttachmentTypesService) {
		super();
	}

	ngOnInit(): void {
		this.form = this.fb.group({
			id: [0, { nonNullable: true }],
			ar_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
			en_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
			gender: [3],
		});
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.checkNoBusinessOrCompany()) this.getList(this.firstPage);
	}

	get formCtrls() {
		return this.form.controls;
	}

	genderList: RadioButtonModel[] = [
		{
			value: EGender.male,
			label: "male",
			cssClass: "col-md-4 col-12",
		},

		{
			value: EGender.female,
			label: "female",
			cssClass: "col-md-4 col-12",
		},

		{
			value: EGender.allofThem,
			label: "allofThem",
			cssClass: "col-md-4 col-12",
		},
	];

	getList(page: number, pageSize?: number) {
		this.page.pageIndex = page;
		if (pageSize) this.page.pageSize = pageSize;
		let params: IPageParams = {
			PageNumber: page,
			pageSize: this.page.pageSize,
		};
		this.attachmentTypsService
			.getList(params)
			.subscribe((res: IResponseList<IAttachmentTypesModel[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
					res.data.map((item: IAttachmentTypesModel) => {
						item.genderName = this.translate(EGender.getValue(item.gender));
					});
					this.dataSource = new MatTableDataSource(res.data);
					this.isLoaded = true;
					this.page.length = res.meta.totalItemCount;
				} else this.errorList(res.message.messages);
			});
	}

	submit() {
		this.submitted = true;
		if (this.form.valid) {
			const model: IAttachmentTypesRequest = {
				Id: this.formCtrls.id.value,
				ar_Name: this.formCtrls.ar_Name.value,
				en_Name: this.formCtrls.en_Name.value,
				gender: this.formCtrls.gender.value,
			};

			model.Id == 0 ? this.create(model) : this.update(model);
		}
	}

	create(model: IAttachmentTypesRequest) {
		this.showLoading();
		this.attachmentTypsService.create(model).subscribe((res: IResponse<IAttachmentTypesModel>) => {
			if (res?.message?.messageType == MessageTypes.Success) {
				this.clearForm();
				this.getList(this.lastPage, this.page.pageSize);
				this.addSuccessfullyMsg(res?.message?.messageTypeName);
			} else this.errorList(res?.message?.messages);
			this.hideLoading();
		});
	}

	update(model: IAttachmentTypesRequest) {
		this.showLoading();
		this.attachmentTypsService.update(model).subscribe((res: IResponse<IAttachmentTypesModel>) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.clearForm();
				this.getList(this.currentPage, this.page.pageSize);
				this.updateSuccessfullyMsg(res?.message?.messageTypeName);
			} else this.errorList(res?.message?.messages);
			this.hideLoading();
		});
	}

	onDelete(attachmentTyps: IAttachmentTypesModel): void {
		let msg = this.msgTranslate(attachmentTyps.ar_Name, attachmentTyps.en_Name);
		this.deleateDialog(msg).subscribe((confirm) => {
			if (confirm) this.Delete(attachmentTyps);
		});
	}

	Delete(attachmentTyps: IAttachmentTypesModel) {
		this.showLoading();
		this.attachmentTypsService.Delete(attachmentTyps.id).subscribe((res: IResponse) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
				if (this.dataSource.data.length == 1 && this.currentPage > 1) {
					this.getList(this.prevPage, this.page.pageSize);
				} else {
					this.getList(this.currentPage, this.page.pageSize);
				}
				if (attachmentTyps.id == this.formCtrls.id.value) this.clearForm();
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	attachmentTypsToEdit(attachmentTyps: IAttachmentTypesRequest) {
		this.form.patchValue(attachmentTyps);
	}

	clearForm() {
		this.form.reset({ id: 0 });
		this.submitted = false;
	}
}
