import { Injectable } from '@angular/core';
import { INotificationDetailsModel, INotificationDetailsRequest, IPersonalNotificationModel, IPersonalNotificationRequest } from '../models/person/personal-notification';
import { IResponse, IResponseList } from 'app/shared/interfaces/responces';
import { AppClientService } from 'app/shared/services/app-client.service';

@Injectable({
  providedIn: 'root'
})
export class PersonalNotificationService extends AppClientService {
  constructor() {
    super();
  }

  getList(params) {
    return this.get<IResponseList<IPersonalNotificationModel[]>>("", {
      params: params,
    });
  }

  Delete(params) {
    return this.delete<IResponse>("", {
      params: params,
    });
  }
  //////////////////

  createPersonalNotification(data: IPersonalNotificationRequest, params) {
    return this.post<IResponse<IPersonalNotificationModel>>("", data, {
      params: params,
    });
  }

  updatePersonalNotificationy(data: IPersonalNotificationRequest, params) {
    return this.post<IResponse<IPersonalNotificationModel>>("", data, {
      params: params,
    });
  }
  // ///

  getNotificationDetails(params) {
    return this.get<IResponseList<INotificationDetailsModel[]>>("", {
      params: params,
    });
  }

  createNotificationDetails(data: INotificationDetailsRequest, params) {
    return this.post<IResponse<INotificationDetailsModel>>("", data, {
      params: params,
    });
  }
}
