import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { AddUserComponent } from "./components/users/add-user/add-user.component";
import { ActivationSetupComponent } from "./components/activation-setup/activation-setup.component";
import { AttachmentTypesComponent } from "./components/attachment-types/attachment-types.component";
import { UserIndexComponent } from "./components/users/user-index/user-index.component";
import { UserCompaniesComponent } from "./components/users/user-companies/user-companies.component";
import { pathMatch } from "app/shared/enums/route-path-match.enum";
import { IsoCodeComponent } from "./components/iso-code/iso-code.component";
import { CompanyCodeComponent } from "./components/company-code/company-code.component";
import { GroupsIndexComponent } from "./components/groups/groups-index/groups-index.component";
import { AddGroupComponent } from "./components/groups/add-group/add-group.component";
import { PersonalNotificationComponent } from "./components/personal-notification/personal-notification-index/personal-notification-index.component";
import { AddPersonalNotificationComponent } from "./components/personal-notification/add-personal-notification/add-personal-notification.component";
import { ViewsManager } from "app/shared/resources/views-manager";

const routes: Routes = [
	{
		path: "activation-setup",
		component: ActivationSetupComponent,
		data: { view: ViewsManager.activation_setup },
	},

	{
		path: "attachment-types",
		component: AttachmentTypesComponent,
		data: { view: ViewsManager.attachment_types },
	},
	{
		path: "users",
		children: [
			{
				path: "",
				redirectTo: "index",
				pathMatch: pathMatch.full,
			},
			{
				path: "index",
				component: UserIndexComponent,
				data: { view: ViewsManager.users_index},
			},
			{
				path: "add",
				component: AddUserComponent,
				data: { view:  ViewsManager.add_user },
			},
			{
				path: "edit/:userId",
				component: AddUserComponent,
				data: { view: ViewsManager.add_user },
			},
		],
	},
	{
		path: "personal-notification",
		children: [
			{
				path: "",
				redirectTo: "index",
				pathMatch: pathMatch.full,
			},
			{
				path: "index",
				component: PersonalNotificationComponent,
				data: { view: ViewsManager.personal_notification_index },
			},
			{
				path: "add",
				component: AddPersonalNotificationComponent,
				data: { view: ViewsManager.add_personal_notification},
			},
			{
				path: "edit/:PersonalId",
				component: AddPersonalNotificationComponent,
				data: { view: ViewsManager.add_personal_notification},
			},
		],
	},
	{
		path: "iso-code",
		component: IsoCodeComponent,
		data: { view: ViewsManager.iso_code},
	},
	{
		path: "company-code",
		component: CompanyCodeComponent,
		data: { view: ViewsManager.company_code },
	},

	{
		path: "groups",
		children: [
			{
				path: "",
				redirectTo: "index",
				pathMatch: pathMatch.full,
			},
			{
				path: "index",
				component: GroupsIndexComponent,
				data: { view: ViewsManager.groups_index },
			},
			{
				path: "add",
				component: AddGroupComponent,
				data: { view: ViewsManager.add_group },
			},
			{
				path: "edit/:groupId",
				component: AddGroupComponent,
				data: { view:  ViewsManager.add_group},
			},
		],
	},
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule],
})
export class GeneralSetupRoutingModule {}
