import { Injectable } from "@angular/core";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { IAttachmentTypesModel, IAttachmentTypesRequest } from "../models/attachment-types";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
	providedIn: "root",
})
export class AttachmentTypesService extends AppClientService {
	constructor() {
		super();
	}

	create(body: IAttachmentTypesRequest) {
		return this.post<IResponse<IAttachmentTypesModel>>("GeneralSetup/AttachmentType/Create", body);
	}

	update(data: IAttachmentTypesRequest) {
		return this.post<IResponse<IAttachmentTypesModel>>("GeneralSetup/AttachmentType/Edit", data);
	}

	getList(params: IPageParams) {
		return this.get<IResponseList<IAttachmentTypesModel[]>>("GeneralSetup/AttachmentType/GetList", {
			params: params,
		});
	}

	getAll() {
		this.get<IResponseList<IAttachmentTypesModel[]>>("GeneralSetup/AttachmentType/GetAll");
	}

	Delete(id: number) {
		return this.delete<IResponse>("GeneralSetup/AttachmentType/Delete", {
			params: {
				id: id,
			},
		});
	}
}
