import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import {
	IActivationSetupModel,
	IActivationSetupRequest,
} from "app/general-setup/models/activation-setup";
import { ActivationSetupService } from "app/general-setup/services/activation-setup.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidator } from "app/shared/validators/custom.validator";

@Component({
	selector: "app-activation-setup",
	templateUrl: "./activation-setup.component.html",
	styleUrls: ["./activation-setup.component.scss"],
})
export class ActivationSetupComponent extends AppHelpers implements OnInit {
	form: FormGroup;
	submitted: boolean = false;
	dataSource: MatTableDataSource<IActivationSetupModel>;
	mandatoryColomns: string[] = ["Ser", "id", "object_Name", "require_Activation", "actions"];
	isLoaded: boolean = false;
	optionalColomns: string[] = [];
	constructor(private activationService: ActivationSetupService) {
		super();
	}

	ngOnInit(): void {
		this.form = this.fb.group({
			id: [0, { nonNullable: true }],
			object_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
			require_Activation: [false],
		});
	}

	get formCtrls() {
		return this.form.controls;
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.checkNoBusinessOrCompany()) this.getList(this.firstPage);
	}

	getList(page: number, pageSize?: number) {
		this.page.pageIndex = page;
		if (pageSize) this.page.pageSize = pageSize;
		let params = {
			PageNumber: page,
			pageSize: this.page.pageSize,
		};
		this.activationService
			.getList(params)
			.subscribe((res: IResponseList<IActivationSetupModel[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
					this.dataSource = new MatTableDataSource(res.data);
					this.isLoaded = true;
					this.page.length = res.meta.totalItemCount;
				} else this.errorList(res.message.messages);
			});
	}

	submit() {
		this.submitted = true;
		if (this.form.valid) {
			const model: IActivationSetupRequest = {
				id: this.formCtrls.id.value,
				object_Name: this.formCtrls.object_Name.value,
				require_Activation: this.formCtrls.require_Activation.value,
			};

			model.id == 0 ? this.create(model) : this.update(model);
		}
	}

	create(model: IActivationSetupRequest) {
		this.showLoading();
		this.activationService.create(model).subscribe((res: IResponse<IActivationSetupModel>) => {
			if (res?.message?.messageType == MessageTypes.Success) {
				this.clearForm();

				this.getList(this.lastPage, this.page.pageSize);
				this.addSuccessfullyMsg(res?.message?.messageTypeName);
			} else this.errorList(res?.message?.messages);
			this.hideLoading();
		});
	}

	update(model: IActivationSetupRequest) {
		this.showLoading();
		this.activationService.update(model).subscribe((res: IResponse<IActivationSetupModel>) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.clearForm();
				this.getList(this.currentPage, this.page.pageSize);
				this.updateSuccessfullyMsg(res?.message?.messageTypeName);
			} else this.errorList(res?.message?.messages);
			this.hideLoading();
		});
	}

	onDelete(activationSetup: IActivationSetupModel): void {
		let msg = this.msgTranslate(activationSetup.object_Name);
		this.deleateDialog(msg).subscribe((confirm) => {
			if (confirm) this.Delete(activationSetup);
		});
	}

	Delete(activationSetup: IActivationSetupModel) {
		this.showLoading();
		this.activationService.Delete(activationSetup.id).subscribe((res: IResponse) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
				if (this.dataSource.data.length == 1 && this.currentPage > 1) {
					this.getList(this.prevPage, this.page.pageSize);
				} else {
					this.getList(this.currentPage, this.page.pageSize);
				}
				if (activationSetup.id == this.formCtrls.id.value) this.clearForm();
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	activationSetupToEdit(activationSetup: IActivationSetupModel) {
		this.form.patchValue(activationSetup);
	}

	clearForm() {
		this.form.reset({ id: 0, require_Activation: false });
		this.submitted = false;
	}
}
