import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import { ICompanyCodeModel, ICompanyCodeRequest } from "../models/company-code";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";

@Injectable({
  providedIn: "root",
})
export class CompanyCodeService extends AppClientService {
  constructor() {
    super();
  }

  create(data: ICompanyCodeRequest) {
    return this.post<IResponse<ICompanyCodeModel>>(
      "GeneralSetup/CompanyCode/Create",
      data
    );
  }

  update(data: ICompanyCodeRequest) {
    return this.post<IResponse<ICompanyCodeModel>>(
      "GeneralSetup/CompanyCode/Edit",
      data
    );
  }

  getCodeStatus(compId: number) {
    return this.get<IResponse<ICompanyCodeModel>>(
      "GeneralSetup/CompanyCode/GetCodeStatus",
      {
        params: {
          compId: `${compId}`,
        },
      }
    );
  }
}
