import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { Params } from "@angular/router";
import { IGroupModel, IGroupRequest } from "app/general-setup/models/groups";
import { GroupsService } from "app/general-setup/services/groups.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";


@Component({
  selector: "app-add-group",
  templateUrl: "./add-group.component.html",
  styleUrls: ["./add-group.component.scss"],
})
export class AddGroupComponent extends AppHelpers implements OnInit {
  groupId: number = 0;
  form: FormGroup;
  submitted: boolean = false;
  constructor(private _groupsService: GroupsService) {
    super();
  }

  ngOnInit(): void {
    this.activatedRoute.params.subscribe((params: Params) => {
			this.groupId = params.groupId ?? 0;
		});

    this.form = this.fb.group({
      id: [0, { nonNullable: true }],
      engName: [null, [Validators.required]],
      arbName: [null, [Validators.required]],
      engDescription: [null, [Validators.required]],
      arbDescription: [null, [Validators.required]],
    });

  }

  get formCtrls() {
    return this.form.controls;
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany() && this.groupId > 0) {
      this.getGroupById();
    }
  }

  submit() {
    this.submitted = true;
    if (this.form.valid) {
      const model: IGroupRequest = {
        id: this.form.value.id,
        engName: this.form.value.engName,
        arbName: this.form.value.arbName,
        engDescription: this.form.value.engDescription,
        arbDescription: this.form.value.arbDescription
      };
      model.id == 0 ? this.create(model) : this.update(model);
    }
  }

  create(model: IGroupRequest) {
    this.showLoading();
    this._groupsService.create(model).subscribe((res: IResponse<IGroupModel>) => {
      if (res?.message?.messageType == MessageTypes.Success) {
        this.clearForm()
        this.addSuccessfullyMsg(res?.message?.messageTypeName);
        this.groupId = res.data.id;
      } else this.errorList(res?.message?.messages);
      this.hideLoading();
    });
  }

  update(model: IGroupRequest) {
    this.showLoading();
    this._groupsService.update(model).subscribe((res: IResponse<IGroupModel>) => {
      if (res?.message?.messageType == MessageTypes.Success) {
        this.clearForm()
        this.updateSuccessfullyMsg(res?.message?.messageTypeName);
      } else {
        this.errorList(res?.message?.messages);
      }
      this.hideLoading();
    });
  }

  getGroupById() {
    this._groupsService
      .getGroupById(this.groupId)
      .subscribe((res: IResponse<IGroupModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.form.patchValue(res.data[0])
        }
      });

  }

  clearForm() {
    this.form.reset({ id: 0 });
    this.submitted = false;
  }
}
