import { Injectable } from "@angular/core";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { IActivationSetupModel, IActivationSetupRequest } from "../models/activation-setup";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
	providedIn: "root",
})
export class ActivationSetupService extends AppClientService {
	constructor() {
		super();
	}

	create(body: IActivationSetupRequest) {
		return this.post<IResponse<IActivationSetupModel>>("GeneralSetup/ActivationSetup/Create", body);
	}

	update(data: IActivationSetupRequest) {
		return this.post<IResponse<IActivationSetupModel>>("GeneralSetup/ActivationSetup/Edit", data);
	}

	getList(params: IPageParams) {
		return this.get<IResponseList<IActivationSetupModel[]>>(
			"GeneralSetup/ActivationSetup/GetList",
			{
				params: params,
			}
		);
	}

	getAll() {
		this.get<IResponse<IActivationSetupModel[]>>("GeneralSetup/ActivationSetup/GetAll");
	}

	Delete(id: number) {
		return this.delete<IResponse>("GeneralSetup/ActivationSetup/Delete", {
			params: {
				id: id,
			},
		});
	}
}
