import { Component, OnInit } from "@angular/core";
import { MatTableDataSource } from "@angular/material/table";
import { IPersonalNotificationModel } from "app/general-setup/models/person/personal-notification";
import { PersonalNotificationService } from "app/general-setup/services/personal-notification.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
	selector: "app-personal-notification",
	templateUrl: "./personal-notification-index.component.html",
	styleUrls: ["./personal-notification-index.component.scss"],
})
export class PersonalNotificationComponent extends AppHelpers implements OnInit {
	submitted: boolean = false;
	mandatoryColomns: string[] = ["Ser", "English Name", "Group", "actions"];
	isLoaded: boolean = false;
	optionalColomns: string[] = [];
	dataSource: MatTableDataSource<IPersonalNotificationModel>;
	constructor(
 		private personalNotificationService: PersonalNotificationService
	) {
		super();
	}

	ngOnInit(): void {
		// if (this.CheckNoBusinessOrCompany()) this.getList(this.currentPage);
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.checkNoBusinessOrCompany())
			this.getList(this.pageFromUrl > 0 ? this.pageFromUrl : this.firstPage);
	}

	getList(page: number, pageSize?: number) {
		this.page.pageIndex = page;
		if (pageSize) this.page.pageSize = pageSize;
		this.removePageParams();
		let params = {
			PageNumber: page,
			pageSize: this.page.pageSize,
		};
		this.personalNotificationService
			.getList(params)
			.subscribe((res: IResponseList<IPersonalNotificationModel[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
					if (res.data.length == 0 && page > 1) {
						this.getList(this.prevPage);
						return;
					}
					this.dataSource = new MatTableDataSource(res.data);
					this.isLoaded = true;
					this.page.length = res.meta.totalItemCount;
				} else this.errorList(res.message.messages);
			});
	}

	onDelete(personalNotification: any): void {
		let msg = this.msgTranslate(personalNotification.name);
		this.deleateDialog(msg).subscribe((confirm) => {
			if (confirm) this.delete(personalNotification);
		});
	}

	delete(personalNotification: IPersonalNotificationModel) {
		let params = {
			...this.businessCompanyParams,
			id: personalNotification.id,
		};
		this.showLoading();
		this.personalNotificationService.Delete(params).subscribe((res: IResponse) => {
			if (res.message.messageType == MessageTypes.Success) {
				if (this.dataSource.data.length == 1 && this.currentPage > 1) {
					this.getList(this.prevPage, this.page.pageSize);
				} else {
					this.getList(this.currentPage, this.page.pageSize);
				}
				this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}
}
